@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: "Work Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    100deg,
    rgba(250, 214, 195, 0.8) 30%,
    #aeeef0 120%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Rose img {
  width: 75%;
  border-radius: 10px;

  -webkit-transform: 0.4s filter ease;

          transform: 0.4s filter ease;
}
.Rose:hover img {
  -webkit-filter: grayscale();
          filter: grayscale();
}
.Rose .underline {
  border: 4 solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 36px;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.1em;
  text-align: center;
  font-size: 1.2rem;
  margin: 10px;
  position: relative;
}
.underline::after {
  width: 0%;
  height: 4px;
  display: block;
  background-color: rgb(235, 205, 231);
  content: " ";
  position: absolute;
  top: 34px;
  left: 50%;
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Rose:hover .underline::after {
  width: 100%;
  top: 34px;
  left: 0;
}

h3 {
  color: rgb(240, 163, 230);
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

