.Rose img {
  width: 75%;
  border-radius: 10px;

  transform: 0.4s filter ease;
}
.Rose:hover img {
  filter: grayscale();
}
.Rose .underline {
  border: 4 solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 36px;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.1em;
  text-align: center;
  font-size: 1.2rem;
  margin: 10px;
  position: relative;
}
.underline::after {
  width: 0%;
  height: 4px;
  display: block;
  background-color: rgb(235, 205, 231);
  content: " ";
  position: absolute;
  top: 34px;
  left: 50%;
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Rose:hover .underline::after {
  width: 100%;
  top: 34px;
  left: 0;
}

h3 {
  color: rgb(240, 163, 230);
}
